.list{
    color : #fff;
    width: 280px;
    background-color: #ff6699;
    height: 40px;
    margin: 20px auto;
    border-radius: 3px;
}
.list p{
    font-size: 15px;
    padding: 5px;
    position: relative;
}
.list p span{
    margin: 5px 8px;
    position: absolute;
    right: 10px;
    font-size: 12px;
    cursor: pointer;
}
.list p input{
    background-color: transparent;
    color: #fff;
    border: 0;
}
.list p input:focus{
    outline: none;
}