*{
  font-family: 'Open Sans', sans-serif !important;
  font-weight: bolder !important;
}
body{
  background-color: #6666ff;
}
.App{
  background-color: #5c00e6;
  min-height: 420px;
  width: 320px;
  margin: 40px auto;
  border-radius: 3px;
}
#to-do-form input{
  background-color: teal;
  color: #ffffff;
  border: 0;
  width: 180px;
  height: 40px;
  padding: 0 10px;
  margin: 20px;
  font-size: 16px;
  border-radius: 3px;
}
#to-do-form input::placeholder{
  color: #ffffff;
  opacity: .5;
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  border: 0;
  width: 60px;
  height: 40px;
  font-size: 16px;
  border-radius: 3px;
  background-color: #ff9966;
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}